import { Link } from "gatsby"
import { default as React } from "react"
import { media } from 'theme'
import {
    // Highlight,
    Hits,
    Index,
    PoweredBy,
} from "react-instantsearch-dom"
import './Search.css'


const PageHit = ({ hit }) => {

    //headingName is needed for link address

    let headingName = ""

    const headings = hit.headings.filter((heading, i) => i < 1 && heading).map(({ value }, i) => {

        headingName = value


        if (value.indexOf('<code class') !== -1) {
            headingName = value.split('>')[1].split('<')[0]
        }
        //The parts like "watch the tutorial are not accessible from search"
        else if (
            (value.indexOf('Watch the') !== -1)
            || (value.indexOf('Watch an') !== -1)
            || (value.indexOf('Watch video') !== -1)
        ) {
            headingName = ""
        }

        return (
            <h5 key={i} css={{
                marginBottom: 10
            }}>
                {headingName}
                {/*  <Highlight attribute="title" hit={hit} tagName="mark" /> */}
            </h5>
        )
    })

    return (
        <>
            {headingName
                ? <Link to={`${hit.slug}#${headingName.replace(/\s/ig, '-').replace(/[\?!.<>,]/ig, '').toLowerCase()}`} >
                    <div
                        className="resultHit"
                        style={{
                            display: 'grid',
                            gridTemplateColumns: '37% 60%',
                            gridGap: '3%',
                            margin: '10px 0',
                        }}>
                        <div css={{
                            fontSize: 20,
                            color: '#777',
                            paddingLeft: 10,
                            borderRight: '1px solid #999',
                            [media.lessThan('large')]: {
                                fontSize: 16,
                            },
                            [media.lessThan('medium')]: {
                                fontSize: 16,
                            },
                        }}>
                            {hit.title}
                        </div>
                        <div css={{
                            fontSize: 20,
                            [media.lessThan('large')]: {
                                fontSize: 16,
                            },
                            [media.lessThan('medium')]: {
                                fontSize: 16,
                            },
                        }}>
                            {headings}
                        </div>
                        {/* <Snippet attribute="excerpt" hit={hit} tagName="mark" /> */}
                    </div>
                </Link >
                : null
            }
        </>

    )
}


const HitsInIndex = ({ index }) => (
    <Index style={{ position: 'absolute' }} indexName={index.name}>
        <Hits className="Hits" hitComponent={PageHit} />
    </Index>
)

const SearchResult = ({ indices, show }) => (
    <div
        className="searchResult"
        css={{
            display: show ? 'block' : 'none',
            // display: 'block',
            right: 0,
            width: 600,
            maxHeight: 400,
            overflow: 'scroll',
            position: 'absolute',
            zIndex: 2,
            borderRadius: 5,
            boxShadow: 'inset 0 0 10px #ddd',
            top: '100%',
            backgroundColor: '#fff',
            color: '#000',
            [media.lessThan('large')]: {
                width: 450,
            },
            [media.lessThan('medium')]: {
                width: 350,
            },
        }}
    >
        {indices.map(index => (
            <HitsInIndex index={index} key={index.name} />
        ))}
        <div css={{
            display: 'flex',
            justifyContent: 'flex-end',
            alignItems: 'center',
            // flexDirection: 'column'

        }}>
            <PoweredBy  />
        </div>
    </div>
)

export default SearchResult